<template>
  <span class="tag-icon" :style="{ backgroundColor: tagColor }">{{
    tagText
  }}</span>
</template>

<script>
import { type } from "./type";
export default {
  props: {
    tagClass: {
      type: String,
      required: true,
    },
  },
  computed: {
    tagText() {
      return type[this.tagClass].text || "文章";
    },
    tagColor() {
      return type[this.tagClass].backgroundColor || "#47bd87";
    },
  },
  mounted() {
    console.log(type);
  },
};
</script>

<style lang="scss" scoped>
.tag-icon {
  color: #ffffff;
  font-size: 8px;
  border-radius: 4px;
  background-color: #47bd87;
  padding: 2px 8px;
}
</style>